  font-style: normal;
  height: 100vh;
  overflow: hidden;
}

/* for the grid */
.blue-container {
  position: relative;
  width: 390px;
  height: 844px;
  padding: auto;
  margin: 0 auto;
  background: #4059AD;
}

.container {
  position: relative;
  width: 390px;
